import { ShowListItem } from "components/ShowList/ShowListItem";

import { ArrowIcon } from "components/icons/ArrowIcon";

import { useHorizontalScroll } from "hooks/useHorizontalScroll";
import { Show } from "types";

function ScrollLeftButton({ onClick }: any) {
  return (
    <button
      className={`absolute top-0 left-0 text-[#ff0000] w-[30px] flex items-center h-full`}
      onClick={onClick}
    >
      <ArrowIcon />
    </button>
  );
}

function ScrollRightButton({ onClick }: any) {
  return (
    <button
      className={`absolute top-0 right-0 text-[#ff0000] w-[30px] rotate-180 flex items-center h-full`}
      onClick={onClick}
    >
      <ArrowIcon />
    </button>
  );
}

interface ShowListProps {
  shows: Show[];
}

export function ShowList({ shows }: ShowListProps) {
  const {
    ref: showListRef,
    scrollLeft,
    scrollRight,
  } = useHorizontalScroll<HTMLUListElement>();

  return (
    <div className="relative mb-8">
      <ScrollLeftButton hidden={false} onClick={scrollLeft} />

      <ul
        ref={showListRef}
        className="flex space-x-2 overflow-x-hidden scroll-smooth"
      >
        {shows.map((show: Show) => (
          <ShowListItem
            key={show.id}
            id={show.id}
            title={show.title}
            image={show.image}
          />
        ))}
      </ul>

      <ScrollRightButton hidden={false} onClick={scrollRight} />
    </div>
  );
}
