import { useParams } from "react-router-dom";

import { ShowList } from "components/ShowList";
import { StarIcon } from "components/icons/StarIcon";

import { Show } from "types";
import { useShow } from "utils/shows";

function BackdropImage({ src = "https://placehold.it/445" }) {
  return (
    <div
      className="show__backdrop -ml-3 -mr-3 bg-cover bg-center pb-[55%] mb-4 lg:w-1/2 lg:shrink-0 lg:m-0"
      style={{ backgroundImage: `url(${src})` }}
    />
  );
}

function ShowMetadata({ show }: {show: Show}) {
  return (
    <div className="show__meta meta text-[#a3a3a3] mb-4">
      <span className="meta__genre">{show.genres}</span>
      <span className="meta__spacer mx-1">|</span>
      <span className="meta__year">{show.year}</span>
      <span className="meta__spacer mx-1">|</span>
      <span className="meta__rating ">
        <StarIcon />
        {show.imDbRating}/10
      </span>
      {show.runtimeStr && (
        <>
          <span className="meta__spacer mx-1">|</span>
          <span className="meta__duration">{show.runtimeStr}</span>
        </>
      )}
    </div>
  );
}

function ShowPage() {
  const params = useParams();
  const showId = params.showId;

  const { show } = useShow(showId ?? "");

  return (
    <>
      <section className="show mb-8">
        <div className="content flex flex-col gap-6 lg:flex-row">
          <BackdropImage src={show?.posters?.backdrops[1] || show?.image} />

          <div>
            <h1 className="show__title text-2xl font-bold">{show?.title}</h1>
            {show !== null ? (<ShowMetadata show={show} />) : null}
            <p className="show__description">{show?.plot}</p>
          </div>
        </div>
      </section>

      {show?.similars?.length && (
        <section className="similar-shows">
          <ShowList shows={show.similars} />
        </section>
      )}
    </>
  );
}

export { ShowPage }