import { useState, useEffect } from "react";

import { BiArrowBack as BackArrowIcon } from "react-icons/bi";
import { SearchIcon } from "components/icons/Search";

import { SearchResultList } from "./SearchResultList";
import { SearchResultItem } from "./SearchResultItem";
import { SearchForm } from "./SearchForm";
import { ErrorMessage, IconButton } from "components/lib";
import { FlashingDotsAnimation } from "assets/animations/FlashingDotsAnimation";

import { useShowSearch } from "utils/shows";

export function OpenSearchBoxButton({ onClick }: any) {
  return (
    <button
      aria-haspopup="true"
      className="open-searchbar p-4 block lg:hidden md:order-1"
      onClick={onClick}
    >
      <SearchIcon />
    </button>
  );
}

interface SearchBoxProps {
  onClose?: () => void;
}

function SearchBox({ onClose }: SearchBoxProps) {
  const [query, setQuery] = useState("");
  const { shows, isLoading, refetch, isRefetching, isError, error } =
    useShowSearch(query);

  useEffect(() => {
    const debounceFn = setTimeout(() => {
      refetch();
    }, 500);

    return () => clearTimeout(debounceFn);
  }, [query, refetch]);

  return (
    <div className="searchbox h-full bg-[#2c2c2c] pr-[1px] flex items-center w-full sm:order-2 lg:order-none lg:w-[350px] lg:shrink-0 lg:grow">
      <IconButton onClick={onClose}>
        <BackArrowIcon color="#fff" fontSize={"1.5rem"} />
      </IconButton>

      <SearchForm query={query} onQueryChange={(query) => setQuery(query)} className="w-full" />

      {query !== "" ? (
        <SearchResultList>
          {isLoading || isRefetching ? <FlashingDotsAnimation /> : null}

          {/* @ts-ignore 2322 */}
          {isError && <ErrorMessage error={error} />}

          {!isLoading && !isRefetching && !isError
            ? shows.map((show) => (
                <SearchResultItem
                  key={show.id}
                  id={show.id}
                  poster={show.image}
                  title={show.title}
                  year={show.description.replace(/[^0-9]/g, "")}
                  topActors={show.starList?.map((s) => s.name).slice(0, 3)}
                />
              ))
            : null}
        </SearchResultList>
      ) : null}
    </div>
  );
}

export { SearchBox }