import { App } from "App";
import { Home } from "pages/home";
import { ShowPage } from "pages/show";
import { GenreShowList } from "components/GenreShowList";
import { NewReleases } from "pages/newReleases";
import { TopShows } from "pages/topShows";
import { aboutUs } from "pages/aboutUs";
import { ComingSoon } from "pages/comingSoon";

interface IPath {
  path?: string;
  element: Function;
  index?: boolean;
  children?: Array<IPath>;
}

const routes: Array<IPath> = [
  {
    path: "/",
    element: App,
    children: [
      {
        index: true,
        element: Home,
      },
      { path: "show/:showId", element: ShowPage },
      { path: "genre/:genre", element: GenreShowList },
      { path: "new-releases", element: NewReleases },
      { path: "coming-soon", element: ComingSoon },
      { path: "top-shows", element: TopShows },
      { path: "about-us", element: aboutUs },
      { path: "*", element: () => "Not found! Go back" },
    ],
  },
];

export { routes };
