import React from "react";

type Props = {
  children: React.ReactNode;
  className?: string;
};

function SearchResultList({ children, className }: Props) {
  const _className = `bg-[#2c2c2c] min-h-screen overflow-hidden z-10
   absolute top-[110%] w-full left-0 ${className ? className : ""}
   rounded-[3px]`;

  return <ul className={_className}>{children}</ul>;
}

export { SearchResultList }