import { RefObject, useCallback, useEffect, useState } from "react";
import { useOnClickOutside } from "usehooks-ts";

function useSearchBox<T extends HTMLElement>(ref: RefObject<T>) {
  const [query, setQuery] = useState("");
  const [searchResultListOpen, setSearchResultListOpen] = useState(false);
  useOnClickOutside(ref, () => setSearchResultListOpen(false));

  const _setQuery = (query: string) => {
    setQuery(query);

    if (query === "") {
      setSearchResultListOpen(false);
    } else {
      setSearchResultListOpen(true);
    }
  };

  const onSearchboxClick = useCallback(() => {
    if (query !== "") {
      setSearchResultListOpen(true);
    }
  }, [query]);

  useEffect(() => {
    const searchboxEl = ref.current;
    searchboxEl?.addEventListener("click", onSearchboxClick);

    return () => searchboxEl?.removeEventListener("click", onSearchboxClick);
  }, [onSearchboxClick, ref]);

  return { query, setQuery: _setQuery, searchResultListOpen };
}

export { useSearchBox }