import { useParams } from "react-router-dom";
import { ShowList } from "components/ShowList";
import { useShowsByGenre } from "utils/shows";
import { Genre, isGenre } from "types";

function GenreShowList() {
  const params = useParams();
  const genre = params.genre || "";
  const {shows} = useShowsByGenre(genre as Genre);

  if (isGenre(genre)) {
    return <ShowList shows={shows} />;
  } else {
    return <p>Invalid genre</p>;
  }
}

export { GenreShowList }