enum Genres {
  "action",
  "horror",
  "adventure",
  "musical",
  "comedy",
  "science fiction",
  "documentary",
  "drama",
  "history",
}

export type Genre = keyof typeof Genres;

export function isGenre(genreStr: string): genreStr is Genre {
  return Object.keys(Genres).includes(genreStr);
}

export interface Show {
  id: string;
  title: string;
  image: string;
  backdrop?: string;
  description?: string;
  starList?: Array<{id: number, name: string}>
  poster?: string;
  posters: { backdrops: string[] } 
  year?: string;
  genres?: Array<string>;
  imDbRating?: Number;
  runtimeStr?: string;
  topActors?: string[];
  plot?: string;
  similars?: Array<Show>;
}
