import { ReactElement } from "react";
import { ShowList } from "components/ShowList";
import { useShowsByGenre } from "utils/shows";
import { ShowListSection } from "components/ShowList/ShowListSection";
import { ShowListSkeleton } from "components/ShowList/ShowListSkeleton";
import { ErrorMessage } from "components/lib";

function Home(): ReactElement {
  const actionShows = useShowsByGenre("action");
  const comedyShows = useShowsByGenre("comedy");
  const horrorShows = useShowsByGenre("horror");
  const documentaryShows = useShowsByGenre("documentary");

  const showLists = [
    {
      title: "Action Movies",
      ...actionShows,
    },
    {
      title: "Comedy Movies",
      ...comedyShows,
    },
    {
      title: "Horror Movies",
      ...horrorShows,
    },
    {
      title: "Documentary Movies",
      ...documentaryShows,
    },
  ];

  return (
    <>
      {showLists.map((showList) => (
        <ShowListSection key={showList.title} title={showList.title}>
          {/* @ts-ignore 2322 */}
          {showList.isError ? <ErrorMessage error={showList.error} /> : null}
          {showList.isLoading ? <ShowListSkeleton /> : null}
          {!showList.isLoading && !showList.isError ? <ShowList shows={showList.shows} /> : null}
        </ShowListSection>
      ))}
    </>
  );
}

export { Home }