import React from "react";

type SearchFormProps = {
  query: string;
  onQueryChange: (query: string) => void;
  className?: string;
};

function SearchForm({
  query,
  onQueryChange,
  className,
}: SearchFormProps) {
  function handleQueryChange(evt: React.ChangeEvent<HTMLInputElement>) {
    const query = evt.target.value;
    onQueryChange(query);
  }

  return (
    <div
      className={`${className ? className : ""} lg:max-w-[100%] hover:outline outline-1 outline-[#366aaa] rounded-[3px] transition-[max-width] duration-300 top-0 left-0 relative`}
    >
      <form id="nav-search-form" method="get" action="/find" className="flex">
        <input
          type="search"
          value={query}
          onChange={handleQueryChange}
          autoFocus
          placeholder="Search Vimus"
          className="w-full h-full p-4 lg:p-2  bg-inherit outline-none text-white"
        />
      </form>
    </div>
  );
}

export { SearchForm }