import { ShowList } from "components/ShowList";
import { ShowListSection } from "components/ShowList/ShowListSection";
import { ShowListSkeleton } from "components/ShowList/ShowListSkeleton";
import { useComingSoonShows } from "utils/shows";

function ComingSoon() {
  const { shows, isLoading } = useComingSoonShows();

  return (
    <ShowListSection title="Coming Soon">
      {isLoading ? <ShowListSkeleton /> : <ShowList shows={shows} />}
    </ShowListSection>
  );
}

export { ComingSoon }