import { useEffect, useRef } from "react";

import { SearchForm } from "./SearchForm";
import { ErrorMessage } from "components/lib";
import { SearchResultList } from "./SearchResultList";
import { SearchResultItem } from "./SearchResultItem";
import { FlashingDotsAnimation } from "assets/animations/FlashingDotsAnimation";

import { useShowSearch } from "utils/shows";
import { useSearchBox } from "hooks/useSearchBox";

function LargeScreenSearchBox() {
  const ref = useRef<HTMLDivElement>(null);
  const { query, setQuery, searchResultListOpen } = useSearchBox(ref);
  const { shows, isLoading, refetch, isError, error } = useShowSearch(query);
  const showsReady = !isLoading && !isError && shows.length > 0;

  useEffect(() => {
    const debounceFn = setTimeout(() => {
      refetch();
    }, 500);

    return () => clearTimeout(debounceFn);
  }, [query, refetch]);

  return (
    <div
      className="searchbox flex-1 max-w-[500px] rounded-[3px] bg-[#2c2c2c] pr-[1px] flex items-center relative"
      ref={ref}
    >
      <SearchForm query={query} onQueryChange={setQuery} className="w-full" />

      {searchResultListOpen ? (
        <SearchResultList className="absolute bottom-0 left-0">
          {isLoading ? <FlashingDotsAnimation /> : null}

          {/* Error actually has message prop */}
          {/* @ts-ignore 2322 */}
          {isError && <ErrorMessage error={error} />}

          {showsReady
            ? shows.map((show) => (
                <SearchResultItem
                  key={show.id}
                  id={show.id}
                  poster={show.image}
                  title={show.title}
                  year={show.description.replace(/[^0-9]/g, "")}
                  topActors={show.starList?.map((s) => s.name).slice(0, 3)}
                />
              ))
            : null}
        </SearchResultList>
      ) : null}
    </div>
  );
}

export { LargeScreenSearchBox }