import { useState } from "react";

import { AppLogo } from "components/AppLogo";
import { SearchBox } from "components/Search";
import { Navbar, ToggleNavbarOpenButton } from "./NavBar";
import { OpenSearchBoxButton } from "components/Search/SearchBox";
import { LargeScreenSearchBox } from "components/Search/LargeScreenSearchBox";

import { useNavbarOpen } from "./useNavbarOpen";
import { useWindowSize } from "hooks/useWindowSize";

function AppBar() {
  const { navbarOpen, setNavbarOpen } = useNavbarOpen();
  const [searchBoxOpen, setSearchBoxOpen] = useState(false);
  const [width] = useWindowSize();
  const isLargeScreen = width > 1024;

  return (
    <header className={`relative flex items-center h-[56px] md:pr-4 justify-end max-w-screen-xl mx-auto`}>
      {searchBoxOpen ? <SearchBox onClose={() => setSearchBoxOpen(false)} /> : (
        <>
          <AppLogo id="app-header-logo" className="w-28 mr-auto" />

          {isLargeScreen ? (<LargeScreenSearchBox />) : null}

          <div className="max-w-screen-xl flex   justify-end items-center md:gap-4">
            <OpenSearchBoxButton onClick={() => setSearchBoxOpen(true)} />

            <ToggleNavbarOpenButton onClick={() => setNavbarOpen(!navbarOpen)} />
            <Navbar className={navbarOpen ? "max-h-screen" : "max-h-0 py-0"} />
          </div>
        </>
      )}
    </header>
  );
}

export { AppBar }