function useComingSoonShows() {
  const queryResult = useQuery<Show[]>({
    queryKey: ["shows", "coming soon"],
    queryFn: () =>
      axios
        .get("ComingSoon/k_k2rf07hj")
        .then((response) => response.data.items),
  });

  return { ...queryResult, shows: queryResult.data ?? [] };
}

interface IStar {
  id: number;
  name: string;
}

interface ISearchResult {
  id: string;
  image: string;
  title: string;
  description: string;
  starList: Array<IStar>;
}

function useShowSearch(query: string) {
  const queryResult = useQuery<ISearchResult[]>({
    queryKey: ["show search", query],
    enabled: query !== "",
    queryFn: ({ signal }) =>
      axios
        .get(`AdvancedSearch/k_k2rf07hj/?title=${query}&count=50`, { signal })
        .then((response) => response.data.results),
  });

  return { ...queryResult, shows: queryResult.data ?? [] };
}

export {
  useShowsByGenre,
  useShow,
  useShowSearch,
  useShowsInTheaters,
  useMostPopularShows,
  useComingSoonShows,
};
