export function IconButton({ children, ...props }: any) {
  return (
    <button
      {...props}
      className="bg-transparent text-black p-[10px] w-[48px] h-[48px] border-0"
    >
      {children}
    </button>
  );
}

interface ErrorMessageProps {
  error: { message: string };
}

function ErrorMessage({ error }: ErrorMessageProps) {
  console.error(error);

  return (
    <div role="alert" className="text-rouse-900">
      <span>There was an error: </span>
      <pre className="whitespace-break-spaces m-0 mb-[-5px]">
        {error.message}
      </pre>
    </div>
  );
}

export { ErrorMessage };
