import { BurgerIcon } from "components/icons/Burger";
import { Link } from "react-router-dom";

const links = [
  {
    to: "/top-shows",
    label: "Top Shows",
  },
  {
    to: "/new-releases",
    label: "New Releases",
  },
  {
    to: "/coming-soon",
    label: "Coming Soon",
  },
];

type Props = {
  className?: string;
};

function Navbar({ className }: Props) {
  const _className = `nav-menu z-10 transition-{max-height, padding} duration-500 
  flex p-4 md:p-0 flex-col md:flex-row absolute md:static top-[56px] left-0 bg-black 
  md:bg-transparent w-full lg:w-auto lg:ml-4 text-white overflow-hidden 
  text-xl space-y-3 ${className} whitespace-nowrap
  md:max-h-screen md:space-x-6 md:space-y-0 md:justify-end
`;

  return (
    <nav>
      <ul
        className={_className}
      >
        {links.map((lnk, i) => (
          <li key={i}>
            <Link to={lnk.to}>{lnk.label}</Link>
          </li>
        ))}
      </ul>
    </nav>
  );
}

function ToggleNavbarOpenButton(props: any) {
  return (
    <button
      onClick={props.onClick}
      className="nav-menu-toggle md:hidden z-10 p-4"
    >
      <BurgerIcon />
    </button>
  );
}

export { Navbar, ToggleNavbarOpenButton }
