import React, { useState } from "react";
import { useLocation } from "react-router-dom";

function useNavbarOpen(initialValue = false) {
  const [navbarOpen, setNavbarOpen] = useState(initialValue);
  const location = useLocation();

  React.useEffect(() => {
    setNavbarOpen(false);
  }, [location]);

  return { navbarOpen, setNavbarOpen };
}

export { useNavbarOpen }