import { ShowListItemSkeleton } from "./ShowListItem";

export function ShowListSkeleton() {
  const skeletonItems = Array(6)
    .fill("x")
    .map((el, i) => <ShowListItemSkeleton key={i} />);

  return (
    <div className="showlist-skeleton mb-8">
      <ul className="flex space-x-2 overflow-x-hidden scroll-smooth">
        {skeletonItems}
      </ul>
    </div>
  );
}
