import { useRef } from "react";

function useHorizontalScroll<T extends HTMLElement>() {
  const scrollOffset = 900;
  const elementRef = useRef<T>(null);

  const scrollRight = () => {
    if (elementRef.current) {
      elementRef.current.scrollLeft += scrollOffset;
    }
  };

  const scrollLeft = () => {
    if (elementRef.current) {
      elementRef.current.scrollLeft -= scrollOffset;
    }
  };

  return { scrollRight, scrollLeft, ref: elementRef };
}

export { useHorizontalScroll }